@import url('../App/App.css');

div.listContainer {
    width: 100%;
    max-height: 70vh;
    /* max-width: 550px; */
    min-width: 430px;
    overflow-y: auto;
    border-radius: 12px;
    margin: 20px;
    padding: 12px 10px;
    /* border: var(--light-color) 1px solid; */
    background: #0000003c;
    backdrop-filter: blur(5px);
    transition: all 200ms;
}
@media (max-width:910px) {
    div.listContainer{
        min-width: 0px;
    }
  }
  

div.listContainer h3 {
    color: var(--light-color);
    text-shadow: 3px 3px 3px #00000069;
    font-size: 1.5rem;
    text-align: center;
    margin: 12px 0 24px 0;
    text-shadow: 0 0 4px rgba(255, 255, 255, 0),
        0 0 15px rgba(255, 255, 255, 0.624),
        0 0 30px rgba(255, 255, 255, 0),
        0 0 40px #5c9ee5,
        0 0 70px #5c9ee5,
        0 0 80px #5c9ee5,
        0 0 100px #5c9ee5;
}